#top-design-container {
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    background-image: url('../static/images/3_practice.jpg');
    height: 700px;
    padding: 200px;
    text-align: left;
    margin-top: 170px;
}

#first-project {
    grid-row: 1/3;
    grid-column: 1/3;
    background-image: url('../static/images/5.jpg');
}

#second-project {
    grid-row: 1/4;
    grid-column: 3/6;
    background-image: url('../static/images/6.jpg');
}

#third-project {
    grid-row: 3/6; 
    grid-column: 1/3;
    background-image: url('../static/images/1.jpg');
}

#fourth-project {
    grid-row: 4/6; 
    grid-column: 3/6;
    background-image: url('../static/images/craft2.jpg');
}

#second-design-container {
    height: 700px;
    margin-top: 100px;
    padding: 100px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    background-image: url('../static/images/wood-background.jpg');
}

#main-grid {
    height: 100%;
    display: grid;
    grid-template-columns: 20% 20% 20% 20% 20%;
    grid-template-rows: 20% 20% 20% 20% 20%;
    min-width: 300px;
}

#frameworks-grid {
    height: 100%;
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-rows: 200px 200px 200px;
}

#craftsmanship-grid {
    height: 100%;
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: 200px 200px 200px 200px;
}

#churchpew-grid {
    height: 100%;
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: 250px 250px 250px;
}

#main-grid-container {
    border-top: 10px solid hsla(0,100%,0%,0.1);
    border-bottom: 10px solid hsla(0,100%,0%,0.1); 
    width: 100%; 
    height: 700px; 
    padding: 50px;
}

#branding-container {
    background-color: white; 
    padding: 30px; 
    height: 100%; 
    width: 100%; 
    text-align: left;
}

#branding-iron-caption {
    text-transform: none; 
    padding: 10px 200px 50px 50px; 
    width: 100%; 
    font-size: 20px;
}

#branding-header {
    font-size: 50px; 
    font-weight: bold; 
    text-align: center;
}

#branding-image {
    width: 100%; 
    height: 40%;
}

#metal-first {
    grid-row: 1/3; 
    grid-column: 1/4;
    background-image: url('../static/images/metal4.jpg');
}

#metal-second {
    grid-row: 3/4; 
    grid-column: 1/2;
    background-image: url('../static/images/metal1.jpg');
}

#metal-third {
    grid-row: 3/4; 
    grid-column: 2/3;
    background-image: url('../static/images/metal2.jpg');
}

#metal-fourth {
    grid-row: 3/4; 
    grid-column: 3/4;
    background-image: url('../static/images/metal3.jpg');
}

#craft-first {
    grid-row: 1/2; 
    grid-column: 1/2;
    background-image: url('../static/images/craft1.jpg');
}

#craft-second {
    grid-row: 1/2; 
    grid-column: 2/3;
    background-image: url('../static/images/craft3.jpg');
}

#craft-third {
    grid-row: 2/4; 
    grid-column: 1/3;
    background-image: url('../static/images/craft4.jpg');
}

#craft-fourth {
    grid-row: 4/5; 
    grid-column: 1/2;
    background-image: url('../static/images/craft2.jpg');
}

#craft-fifth {
    grid-row: 4/5; 
    grid-column: 2/3;
    background-image: url('../static/images/7.jpg');
}

#church-first {
    grid-row: 1/3; 
    grid-column: 1/3;
    background-image: url('../static/images/2.jpg');
}

#church-second {
    grid-row: 3/4; 
    grid-column: 1/2;
    background-image: url('../static/images/4.jpg');
}

#church-third {
    grid-row: 3/4; 
    grid-column: 2/3;
    background-image: url('../static/images/3.jpg');
}

.main-grid-item-static {
    margin: 3px;
    transition: 0.3s;
    background-image: url('../static/images/5.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    min-width: 200px;
}

.main-grid-item {
    margin: 3px;
    transition: 0.3s;
    background-image: url('../static/images/5.jpg');
    background-repeat: no-repeat;
    background-size: contain;
    width: 100%;
    min-width: 200px;
}

.main-grid-item:hover {
    box-shadow: 0px 13px 25px -4px rgba(0,0,0,0.75);
    cursor: pointer;
    transition: 0.3s;
}

.static-project {
    font-size: 80px; 
    font-weight: bold;
    height: 100px;
    padding: 50px;
}

.grid-container {
    width: 100%; 
    padding: 50px;
}


h1 {
    font-size: 80px; 
    font-weight: bold;
    height: 100px;
    margin-bottom: 10px;
}

@media screen and (max-width: 1320px) {
    #branding-iron-caption {
        padding: 10px 100px 50px 50px;
    }
}


@media screen and (max-width: 1230px) {
    #project-header {
        font-size: 60px;
    }
}

@media screen and (max-width: 1200px) {
    #branding-iron-caption {
        padding: 10px 75px 50px 50px;
    }

    .static-project {
        font-size: 60px;
    }
}

@media screen and (max-width: 1160px) {
    #branding-iron-caption {
        padding: 10px 40px 50px 50px;
    }
}

@media screen and (max-width: 1120px) {
    #branding-iron-caption {
        padding: 10px 10px 50px 50px;
    }
}

@media screen and (max-width: 1090px) {
    #branding-iron-caption {
        padding: 0px;
        font-size: 15px;
    }
}

@media screen and (max-width: 1100px) {
    #main-grid {
        height: 100%;
        display: grid;
        grid-template-columns: 100%;
        grid-template-rows: 25% 25% 25% 25%;
    }

    #first-project {
        grid-row: 1/2;
        grid-column: 1/3;
    }
    
    #second-project {
        grid-row: 2/3;
        grid-column: 1/2;
    }
    
    #third-project {
        grid-row: 3/4; 
        grid-column: 1/3;
    }
    
    #fourth-project {
        grid-row: 4/5; 
        grid-column: 1/3;
    }
}

@media screen and (max-width: 965px) {
    .static-project {
        font-size: 40px;
    }
}

@media screen and (max-width: 730px) {
    #branding-header {
        font-size: 40px;
    }
}

@media screen and (max-width: 720px) {
    .grid-container {
        padding: 0px;
    }
}

@media screen and (max-width: 680px) {
    #branding-iron-caption {
        display: none;
    }

    #branding-image {
        width: 100%; 
        height: 80%;
    }

    #branding-header {
        font-size: 30px;
    }

    #church-headers {
        font-size: 30px;
    }
}

@media screen and (max-width: 635px) {
    #frameworks-grid {
        grid-template-columns: auto;
        grid-template-rows: 200px 200px 200px 200px;
    }
    
    #craftsmanship-grid {
        grid-template-columns: auto;
    }
    
    #churchpew-grid {
        grid-template-columns: auto;
    }

    #metal-first {
        grid-row: 1/2; 
        grid-column: 1/2;
    }
    
    #metal-second {
        grid-row: 2/3; 
        grid-column: 1/2;
    }
    
    #metal-third {
        grid-row: 3/4; 
        grid-column: 1/2;
    }
    
    #metal-fourth {
        grid-row: 4/5; 
        grid-column: 1/2;
    }
    
    #craft-first {
        grid-row: 1/2; 
        grid-column: 1/2;
    }
    
    #craft-second {
        grid-row: 2/3; 
        grid-column: 1/2;
    }
    
    #craft-third {
        grid-row: 3/4; 
        grid-column: 1/2;
    }
    
    #craft-fourth {
        grid-row: 4/5; 
        grid-column: 1/2;
    }
    
    #craft-fifth {
        grid-row: 5/6; 
        grid-column: 1/2;
    }
    
    #church-first {
        grid-row: 1/2; 
        grid-column: 1/2;
    }
    
    #church-second {
        grid-row: 2/3; 
        grid-column: 1/2;
    }
    
    #church-third {
        grid-row: 3/4; 
        grid-column: 1/2;
    }
}

@media screen and (max-width: 620px) {
    #top-design-container {
        padding: 200px 0px 0px 0px;
    }

    #main-grid-container {
        padding: 0px;
        width: 100%;
    }
    #main-grid {
        width: 100%;
    }

    #project-header {
        font-size: 45px;
        height: 60px;
    }
}

@media screen and (max-width: 590px) {
    .static-project {
        font-size: 30px;
        padding: 20px;
    }
}

@media screen and (max-width: 480px) {
    #project-header {
        font-size: 35px;
        margin-bottom: 20px;
    }

    #second-design-container {
        padding: 20px;
    }

    #branding-image {
        height: 95%;
    }
}

@media screen and (max-width: 440px) {
    #branding-header {
        font-size: 20px;
    }

    #branding-container {
        padding: 10px; 
    }
}

@media screen and (max-width: 340px) {
    .static-project {
        font-size: 25px;
    }
}